#list-col1 {
    position: relative;
    left: 35%;
}
#list-col2 {
    position: relative;
    left: 25%;
}
.position-duties {
    list-style-type: disc;
}
@media only screen and (max-width: 768px) {
    #list-col1 {
        position: relative;
        left: 0%;
    }
    #list-col2 {
        position: relative;
        left: 70%;
        bottom: 165px;
    }
}
@media only screen and (max-width: 540px) {
    #list-col1 {
        position: relative;
        left: 15%;
    }
    #list-col2 {
        position: relative;
        left: 70%;
        bottom: 165px;
    }
}


